import React from 'react';
import './Project.css';
import Modal from 'react-modal';

import {
	disableBodyScroll,
	enableBodyScroll,
	clearAllBodyScrollLocks
} from 'body-scroll-lock';

Modal.setAppElement('#root');

export default class Project extends React.Component {
	constructor() {
		super();
		this.state = {
			showModal : false
		};

		this.handleOpenModal = this.handleOpenModal.bind(this);
		this.handleCloseModal = this.handleCloseModal.bind(this);
	}

	targetElement = null;

	async handleOpenModal() {
		await this.setState({ showModal: true });
		this.targetElement = document.querySelector('.ReactModal__Content');
		disableBodyScroll(this.targetElement);
	}

	handleCloseModal() {
		this.setState({ showModal: false });
		enableBodyScroll(this.targetElement);
	}

	componentWillUnmount() {
		clearAllBodyScrollLocks();
	}

	generateImgs(imgs) {
		if (this.props.direction === 'column')
			return (
				<div className="app-imgs-container-col">
					{imgs.map((img) => (
						<div className="app-img-container" key={img.id}>
							<img key={img.id} src={img.img} alt="" />
						</div>
					))}
				</div>
			);
		else
			return (
				<div className="app-imgs-container">
					{imgs.map((img) => (
						<div className="app-img-container" key={img.id}>
							<img key={img.id} src={img.img} alt="" />
						</div>
					))}
				</div>
			);
	}

	render() {
		const { thumbnail, name, description, images } = this.props;
		return (
			<div className="thumbnail-container">
				<img src={thumbnail} onClick={this.handleOpenModal} alt="" />
				<Modal
					closeTimeoutMS={300}
					isOpen={this.state.showModal}
					contentLabel="modal-content"
					className="Modal"
					overlayClassName="Overlay"
				>
					<div
						className="close-button"
						onClick={this.handleCloseModal}
					>
						<span>X</span>
					</div>
					<h1>{name}</h1>
					{this.props.linkText === 'hyper-tempo.com' && (
						<a href={this.props.link}>
							{this.props.linkText}
							<br />
							<br />
						</a>
					)}
					<p>{description}</p>
					{this.generateImgs(images)}
				</Modal>
			</div>
		);
	}
}
